/**
 * 
 */
export enum MethodType {
    GET,
    POST,
    DELETE,
    PUT,
    HEAD
}

export interface IPointer {
    from: string;
    to: string;
}

/**
 * 
 */
export interface IJpp {
    set?: Array<IPointer>,
    copy?: Array<IPointer>,
    move?: Array<IPointer>,
    del?: Array<string>
}

/**
 * 
 */
export interface IInterfaceModel {
    // 
    key: string;
    // 
    method: MethodType;
    // 
    address: string;
    // 
    port?: number;
    // 
    path: string;
    // key
    serverKey?: string;
    // ,false 
    params?: boolean | Object;
    // restful
    isRestful: boolean;
    // 
    jpp?: IJpp;
    // header
    header?: IJpp;
    // putdelete
    idFieldPath?: string;
    // 
    config?: Object;
}


